"use client";

import { useState, useCallback } from "react";
import type { Product } from "sam-shared";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  Table,
  TableHeader,
  TableRow,
  TableHead,
  TableBody,
  TableCell,
} from "@/components/ui/table";
import axios from "axios";

export default function ImportProductsPage() {
  const [harFile, setHarFile] = useState<File | null>(null);
  const [products, setProducts] = useState<Product[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files) {
      setHarFile(event.target.files[0]);
    }
  };

  const handleParse = useCallback(async () => {
    if (!harFile) {
      setError("请先选择一个HAR文件");
      return;
    }
    setIsLoading(true);
    setError(null);
    setProducts([]);

    try {
      const fileContent = await harFile.text();
      const response = await axios.post(
        "http://localhost:3000/products/parse",
        fileContent,
        { headers: { "Content-Type": "text/plain" } }
      );
      setProducts(response.data);
    } catch (err) {
      setError("解析HAR文件失败，请检查文件格式或API服务。");
      console.error(err);
    } finally {
      setIsLoading(false);
    }
  }, [harFile]);

  const handleRemoveProduct = (spuId: string) => {
    setProducts((prev) => prev.filter((p) => p.spuId !== spuId));
  };

  const handleSave = useCallback(async () => {
    if (products.length === 0) {
      setError("没有可保存的商品。");
      return;
    }
    setIsLoading(true);
    setError(null);

    try {
      await axios.post("http://localhost:3000/products/upsert", products);
      alert("商品保存成功！");
      setProducts([]); // Clear the list after saving
    } catch (err) {
      setError("保存商品失败，请检查API服务。");
      console.error(err);
    } finally {
      setIsLoading(false);
    }
  }, [products]);

  return (
    <div className="container mx-auto py-10">
      <div className="flex flex-col gap-4">
        <h1 className="text-2xl font-bold">从HAR文件导入商品</h1>

        <div className="flex gap-2">
          <Input type="file" accept=".har" onChange={handleFileChange} />
          <Button onClick={handleParse} disabled={!harFile || isLoading}>
            {isLoading ? "解析中..." : "解析HAR文件"}
          </Button>
        </div>

        {error && <p className="text-red-500">{error}</p>}

        {products.length > 0 && (
          <>
            <h2 className="text-xl font-bold mt-6">预览商品</h2>
            <p>共找到 {products.length} 件商品。您可以删除不需要的商品，然后保存。</p>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>图片</TableHead>
                  <TableHead>标题</TableHead>
                  <TableHead>价格</TableHead>
                  <TableHead>库存</TableHead>
                  <TableHead>操作</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {products.map((product) => (
                  <TableRow key={product.spuId}>
                    <TableCell>
                      <img src={product.image} alt={product.title} className="w-16 h-16 object-cover" />
                    </TableCell>
                    <TableCell>{product.title}</TableCell>
                    <TableCell>{product.priceInfo?.[0]?.price ?? "N/A"}</TableCell>
                    <TableCell>{product.stockInfo?.stockQuantity ?? "N/A"}</TableCell>
                    <TableCell>
                      <Button
                        variant="destructive"
                        size="sm"
                        onClick={() => handleRemoveProduct(product.spuId)}
                      >
                        删除
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
            <div className="flex justify-end mt-4">
                <Button onClick={handleSave} disabled={isLoading || products.length === 0}>
                    {isLoading ? "保存中..." : "保存到数据库"}
                </Button>
            </div>
          </>
        )}
      </div>
    </div>
  );
} 